import React from 'react';
import {Container, Typography, Grid, Fab} from '@mui/material';
import { MainTable } from './MainTable';
import { ModalManager } from './ModalManager';
import {useNavigate} from 'react-router-dom';
import AddIcon from '@mui/icons-material/Add';


const titleStyle = {
  padding: 0,
  display: 'flex'
}

const titleContainerStyle = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center'
}

const App = () => {
  const navigate = useNavigate()

  const pushToAddUser = () => {
    navigate('/?modal=add-user')
  }

  const pushToAddExercise = () => {
    navigate('/?modal=add-exercise')
  }

  return (
    <div className="App">
      <Container sx={titleContainerStyle}>
        <Grid container alignItems="center">
          <Grid item xs={8}>
            <Typography sx={titleStyle} variant="h2" component="div" gutterBottom>
              Mark's Shitty Gym Tracker
            </Typography>
          </Grid>
          <Grid item xs={2}>
            <Fab variant="extended" color="primary" onClick={pushToAddUser}>
                <AddIcon sx={{ mr: 1 }} />
                Add Degen
            </Fab>
          </Grid>
          <Grid item xs={2}>
            <Fab variant="extended" color="primary" onClick={pushToAddExercise}>
                <AddIcon sx={{ mr: 1 }} />
                Add Exercise
            </Fab>
          </Grid>
        </Grid>
      </Container>
      <MainTable />
      <ModalManager />
    </div>
  );
}

export default App;
