import React, {useContext, useEffect} from 'react';
import {UserContext} from './Provider';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import {useNavigate} from 'react-router-dom';
import { AddWorkoutEntryForm } from './AddWorkoutEntryForm';



const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '60vw',
  bgcolor: 'background.paper',
  boxShadow: 24,
  p: 4,
  outline: 'none'
};

const modalStyle = {
  outline: 'none'
}


export const AddWorkoutEntryModal = () => {
  const {targetExercise, setTargetExercise} = useContext(UserContext);
  const navigate = useNavigate();

  useEffect(() => {
    if (!targetExercise) {
      const sessionTargetExercise = JSON.parse(sessionStorage.getItem('targetExercise') ?? '');
      setTargetExercise(sessionTargetExercise)
    }
  }, [targetExercise, setTargetExercise])

  const handleClose = () => navigate('/');
  return (
    <Modal
      sx={modalStyle}
      open={true}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box sx={style}>
        <AddWorkoutEntryForm />
      </Box>
    </Modal>
  );
}