import React, {useContext, useState} from 'react';
import axios from 'axios';
import {
  Container,
  FormControl,
  TextField,
  Button,
  Grid,
  Typography,
} from '@mui/material';
import { Date, Effort } from './types';
import { useNavigate } from 'react-router-dom';
import { UserContext } from './Provider';

interface FormValues {
  name: string,
}

type PossibleFormTypes = string | Date | number | Effort;

const defaultFormValues = {
  name: '',
}

export const AddExerciseForm = () => {
  const {updateExercises} = useContext(UserContext)
  const [formValues, setFormValues] = useState<FormValues>(defaultFormValues);
  const navigate = useNavigate();
  
  const updateForm = (field: {[key: string]: PossibleFormTypes}) => {
    const newValues = {
      ...formValues,
      ...field,
    }
    setFormValues(newValues);
  }

  const submitForm = async () => {
    const result = await axios.post('/workout', formValues)

    if (result.data === 'Success!') {
      updateExercises();
      // TODO: refactor to do state history update more efficiently
      navigate('/')
    }
  }
  return (<>
    <Container>
      <Typography variant="h5" component="div" align="center">
          Add New Exercise
      </Typography>
      <br/>
      <FormControl fullWidth>
        <Grid container spacing={5}>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Workout Name"
              variant="outlined"
              value={formValues.name}
              onChange={(e) => updateForm({name: e.target.value})}
              />
            </Grid>
          <Grid item xs={12}>
            <Grid container direction="row" justifyContent="flex-end">
              <Button size="large" type="submit" variant="contained" onClick={submitForm}>Submit</Button>
            </Grid>
          </Grid>
        </Grid>
      </FormControl>
    </Container>
  </>)
}