import React, {useContext} from 'react';
import {UserContext} from './Provider'
import {pick} from 'lodash';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import { styled } from '@mui/material/styles';
import {Workout} from './types';
import {useNavigate} from 'react-router-dom';

const Item = styled(Paper)(() => ({
  textAlign: 'center',
  height: 60,
  lineHeight: '60px',
  '&:hover': {
    background: "#F0F0F0",
 },
}));

interface Props {
  workouts: Workout[],
  userId: string,
}

export const WorkoutTable: React.FC<Props> = ({workouts, userId}) => {
  const {setTargetExercise, setTargetUser} = useContext(UserContext);
  const navigate = useNavigate();

  const handleWorkoutClick = (workout: Workout) => {
    const exercise = pick(workout, ['id', 'name'])
    setTargetUser(userId)
    setTargetExercise(exercise)
    navigate('/?modal=workout-history')
  }

  return (
    <>
      <Table size="small" aria-label="collapsible table">
      <TableHead>
        <TableRow>
          <TableCell>
            <Typography variant="h6" gutterBottom component="div" align="center">
              Logged Exercises
            </Typography>
          </TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {workouts.map((workout: Workout) => (
          <TableRow key={workout.id}>
            <TableCell component="th" scope="row" align="center" sx={{border: 'none'}}>
              <Item onClick={() => handleWorkoutClick(workout)} variant="outlined" sx={{backgroundColor: '#F5F5F5'}}>
                {workout.name}
              </Item>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>

  </>
  )
}