import React, {useEffect, useContext} from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Collapse from '@mui/material/Collapse';
import Container from '@mui/material/Container';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import { User} from './types';
import { WorkoutTable } from './WorkoutTable';
import {UserContext} from './Provider'
import { useNavigate } from 'react-router-dom';


function UserRow({user, open}: {user: User, open: boolean}) {
  const {setTargetUser} = useContext(UserContext);
  // const [open, setOpen] = useState(false);
  const navigate = useNavigate();

  const handleOpen = () => {
    if (open) {
      setTargetUser('')
    } else {
      setTargetUser(user.id)
    }
  }

  const handleAddExercise = () => {
    navigate('/?modal=select-workout');
  }

  return (
    <>
      <TableRow sx={{ '& > *': { borderBottom: 'unset' }, backgroundColor: open ? '#F5F5F5' : ''}} hover onClick={handleOpen}>
        <TableCell size="small">
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
        </TableCell>
        <TableCell component="th" scope="row">
          <Typography variant="h6" gutterBottom component="div">
            {user.name}
          </Typography>
        </TableCell>
      </TableRow>
      <TableRow sx={{backgroundColor: '#F5F5F5'}}>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
                {
                  user.workouts.length
                  ? <WorkoutTable workouts={user.workouts} userId={user.id}/>
                  : (
                    <Typography variant="subtitle1" gutterBottom component="div" align="center">
                      No workouts logged!
                    </Typography>
                  )
                }
              <Container sx={{display: 'flex', justifyContent: 'center'}}>
                <Button onClick={handleAddExercise}>+ Add Exercise</Button>
              </Container>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </>
  );
}


export const MainTable = () => {
  const { users, updateUsers, targetUser } = useContext(UserContext);
  useEffect(() => {
    updateUsers();
  }, [])

  const renderNoUsers = () => {
    return (
      <Typography variant="h5" component="div" align="center">
        Add a user dammit!
      </Typography>
    )
  }

  const renderUserTable = () => {
    return (
      <TableContainer component={Paper}>
        <Table aria-label="collapsible table">
          <TableHead>
            <TableRow>
              <TableCell size="small"></TableCell>
              <TableCell>
                <Typography variant="h5" component="div"> 
                  Name
                </Typography>
              </TableCell>
              {/* <TableCell align="right">Consistency</TableCell> */}
              {/* <TableCell align="right">Fat&nbsp;(g)</TableCell>
              <TableCell align="right">Carbs&nbsp;(g)</TableCell>
              <TableCell align="right">Protein&nbsp;(g)</TableCell> */}
            </TableRow>
          </TableHead>
          <TableBody>
            {users.map((user: User) => (
              <UserRow key={user.name} user={user} open={targetUser?.id === user.id} />
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    );
  }

  return users.length ? renderUserTable() : renderNoUsers();
}