export type Date = any

export enum Effort {
  SuperLight = "SuperLight",
  Light = "Light",
  Moderate = "Moderate",
  Heavy = "Heavy",
  AllOut = "AllOut",
}

export interface WorkoutHistory {
  id: string,
  date: Date,
  weight: number,
  reps: number,
  effort: Effort,
}

export interface Exercise {
  id: string,
  name: string,
}

export interface Workout {
  id: string,
  name: string,
  history: WorkoutHistory[],
}

export interface User {
  id: string, 
  name: string,
  workouts: Workout[],
}