import axios from 'axios';
import { pick } from 'lodash';
import React, { useReducer } from 'react';
import {Exercise, User, Workout} from './types';

interface State {
  users: User[],
  updateUsers: () => void,
  targetUser: User | null,
  setTargetUser: (userId: string) => void,
  exercises: Exercise[],
  updateExercises: () => void,
  targetExercise: Exercise | null,
  setTargetExercise: (exercise: Exercise) => void,
}

const initialState: State = {
  users: [],
  updateUsers: () => {},
  targetUser: null,
  setTargetUser: () => {},
  exercises: [],
  updateExercises: () => {},
  targetExercise: null,
  setTargetExercise: () => {},
}

enum UserAction {
  UPDATE_USERS,
  SET_TARGET_USER,
  SET_TARGET_EXERCISE,
  UPDATE_EXERCISES,
}

type Action = 
  {
    type: UserAction.UPDATE_USERS,
    data: User[]
  } |
  {
    type: UserAction.SET_TARGET_USER,
    data: User | null
  } | 
  {
    type: UserAction.SET_TARGET_EXERCISE,
    data: Exercise
  } |
  {
    type: UserAction.UPDATE_EXERCISES,
    data: Exercise[]
  }



export const UserContext = React.createContext(initialState)

const usersReducer = (prevState: State, action: Action): State => {
  switch (action.type) {
    case UserAction.UPDATE_USERS:
      return {
        ...prevState,
        users: action.data,
      };
      case UserAction.SET_TARGET_USER:
        return {
          ...prevState,
          targetUser: action.data,
        };
      case UserAction.SET_TARGET_EXERCISE:
        return {
          ...prevState,
          targetExercise: action.data,
        };
      case UserAction.UPDATE_EXERCISES:
        return {
          ...prevState,
          exercises: action.data,
        };
    default:
      return prevState;
  }
};

interface Props {
  children: JSX.Element;
}

export const Provider: React.FC<Props> = ({children}) => {
  const [state, dispatch] = useReducer(usersReducer, initialState);

  const value = {
    users: state.users,
    updateUsers: async () => {
      const {data}: {data: User[]} = await axios.get('/user');
      dispatch({ type: UserAction.UPDATE_USERS, data });

      if (state.targetUser) {
        const updatedTargetUser = data.find(u => u.id === state.targetUser?.id) as User;
        dispatch({ type: UserAction.SET_TARGET_USER, data: updatedTargetUser });

        if (state.targetExercise) {
          const updatedWorkout = updatedTargetUser.workouts.find(w => w.id === state.targetExercise?.id) as Workout;
          const exerciseVersion = pick(updatedWorkout, ['id', 'name'])
          dispatch({ type: UserAction.SET_TARGET_EXERCISE, data: exerciseVersion });
        }
      }

    },
    targetUser: state.targetUser,
    setTargetUser: (userId: string) => {
      sessionStorage.setItem('targetUserId', userId);
      if (!userId.length) {
        dispatch({ type: UserAction.SET_TARGET_USER, data: null })
      }
      const user = state.users.find(u => u.id === userId) as User;
      dispatch({ type: UserAction.SET_TARGET_USER, data: user });
    },
    exercises: state.exercises,
    updateExercises: async () => {
      const {data}: {data: User[]} = await axios.get('/workout');
      dispatch({ type: UserAction.UPDATE_EXERCISES, data });
    },
    targetExercise: state.targetExercise,
    setTargetExercise: (exercise: Exercise) => {
      sessionStorage.setItem('targetExercise', JSON.stringify(exercise))
      dispatch({ type: UserAction.SET_TARGET_EXERCISE, data: exercise });
    }
  };

  return (
      <UserContext.Provider value={value}>
        {children}
      </UserContext.Provider>
  )
}
