import React, {useEffect, useState, useContext} from 'react';
import axios from 'axios';
import moment from 'moment';
import {
  Container,
  FormControl,
  Input,
  TextField,
  Button,
  ButtonGroup,
  Slider,
  Grid,
  Typography,
} from '@mui/material';
import { Date, Effort } from './types';
import { BasicDatePicker } from './BasicDatePicker';
import { UserContext } from './Provider';
import { useNavigate } from 'react-router-dom';

const maxSliderVal = 30;
const minSliderVal = 1;

interface EntryFormValues {
  name: string,
  date: Date,
  weight: number,
  reps: number,
  effort: Effort,
}

type PossibleFormTypes = string | Date | number | Effort;

const defaultFormValues = {
  name: '',
  date: moment(),
  weight: 0,
  reps: 15,
  effort: Effort.SuperLight,
}

export const AddWorkoutEntryForm = () => {
  const {targetUser, targetExercise, setTargetExercise, setTargetUser, updateUsers} = useContext(UserContext);
  const [formValues, setFormValues] = useState<EntryFormValues>(defaultFormValues);
  const navigate = useNavigate();

  useEffect(() => {
    if (!targetExercise) {
      const sessionTargetExercise = JSON.parse(sessionStorage.getItem('targetExercise') ?? '');
      setTargetExercise(sessionTargetExercise)
    }

    updateForm({name: targetExercise?.name ?? ''});
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [targetExercise, setTargetExercise]);

  useEffect(() => {
    if (!targetUser) {
      const sessionTargetUserId = sessionStorage.getItem('targetUserId') ?? '';
      setTargetUser(sessionTargetUserId)
    }
  }, [targetUser, setTargetUser])



  const updateForm = (field: {[key: string]: PossibleFormTypes}) => {
    const newValues = {
      ...formValues,
      ...field,
    }
    setFormValues(newValues);
  }

  const submitForm = async () => {
    if (isNaN(formValues.weight)) {
      return;
    }

    const payload = {
      userId: targetUser?.id ?? '',
      workoutData: {
        id: targetExercise?.id ?? '',
        ...formValues,
      }
    };

    const result = await axios.post('/userWorkout', payload)

    if (result.data === 'Success!') {
      // TODO: refactor to do state history update more efficiently
      updateUsers()
      navigate('/?modal=workout-history')
    }
  }

  const handleEffortButtonClick = (effort: Effort) => {
    updateForm({effort})
  }

  const handleRepSliderChange = (event: Event, newValue: number | number[]) => {
    if (Array.isArray(newValue)) {
      updateForm({reps: newValue[0] ?? formValues.reps});
    }
    updateForm({reps: newValue})
  }
  

  const handleRepInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    updateForm({reps: Number(event.target.value)});
  }

  const handleRepBlur = () => {
    if (formValues.reps < minSliderVal) {
      updateForm({reps: minSliderVal});
    } else if (formValues.reps > maxSliderVal) {
      updateForm({reps: maxSliderVal});
    }
  };
  return (<>
    <Container>
        <Typography variant="h5" component="div" align="center">
            Add {targetExercise?.name}
        </Typography>
      <br/>
      <FormControl fullWidth>
      <Grid container spacing={5}>
        <Grid item xs={12}>
          <TextField
            disabled
            label="Workout Name"
            variant="outlined"
            value={formValues.name}
            onChange={(e) => updateForm({name: e.target.value})}
            />
          </Grid>
        <Grid item xs={12}>
          <BasicDatePicker
            date={formValues.date}
            setDate={(value: Date) => updateForm({date: value})}
          />
        </Grid>
        <Grid item xs={12}>
          <ButtonGroup
            size="large"
            variant="outlined"
            aria-label="outlined primary button group"
            fullWidth
            >
            <Button
              onClick={() => handleEffortButtonClick(Effort.SuperLight)}
              variant={formValues.effort === Effort.SuperLight ? 'contained' : 'outlined'}
              >Super Light</Button>
            <Button
              onClick={() => handleEffortButtonClick(Effort.Light)}
              variant={formValues.effort === Effort.Light ? 'contained' : 'outlined'}
              >Light</Button>
            <Button 
              onClick={() => handleEffortButtonClick(Effort.Moderate)}
              variant={formValues.effort === Effort.Moderate ? 'contained' : 'outlined'}
              >Moderate</Button>
            <Button 
              onClick={() => handleEffortButtonClick(Effort.Heavy)}
              variant={formValues.effort === Effort.Heavy ? 'contained' : 'outlined'}
              >Heavy</Button>
            <Button 
              onClick={() => handleEffortButtonClick(Effort.AllOut)}
              variant={formValues.effort === Effort.AllOut ? 'contained' : 'outlined'}
              >All-Out</Button>
          </ButtonGroup>
        </Grid>
        <Grid item xs={12}>
          <TextField
            label="Total Weight"
            variant="outlined"
            value={formValues.weight || ''}
            error={isNaN(formValues.weight)}
            helperText={isNaN(formValues.weight) ? "Not a number" : ""}
            onChange={(e) => updateForm({weight: e.target.value})}
            />
          </Grid>
        <Grid item xs={12}>
          <Typography id="input-slider" gutterBottom>
            Reps
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={8}>
              <Slider
                  valueLabelDisplay="auto"
                  marks
                  step={1}
                  max={maxSliderVal}
                  min={minSliderVal}
                  value={formValues.reps}
                  onChange={handleRepSliderChange}
                  aria-labelledby="input-slider"
                  />
            </Grid>
            <Grid item xs={4}>
              <Input
                value={formValues.reps}
                onChange={handleRepInputChange}
                onBlur={handleRepBlur}
                inputProps={{
                  min: 0,
                  max: maxSliderVal,
                  type: 'number',
                  'aria-labelledby': 'input-slider',
                }}
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Grid container direction="row" justifyContent="flex-end">
            <Button size="large" type="submit" variant="contained" onClick={submitForm}>Submit</Button>
          </Grid>
        </Grid>
        </Grid>
      </FormControl>
    </Container>
  </>)
}