import React, {useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import { WorkoutHistoryModal } from './WorkoutHistoryModal';
import queryString from 'query-string';
import { AddWorkoutEntryModal } from './AddWorkoutEntryModal';
import { AddExerciseModal } from './AddExerciseModal'
import { SelectExerciseForEntryModal } from './SelectExerciseForEntryModal';
import { AddUserModal } from './AddUserModal';


interface ModalMap {
  [key: string]: Function;
}

const modalMap: ModalMap = {
  'workout-history': WorkoutHistoryModal,
  'add-workout-entry': AddWorkoutEntryModal,
  'add-exercise': AddExerciseModal,
  'select-workout': SelectExerciseForEntryModal,
  'add-user': AddUserModal,
}

const getModalName = (query: string): string => {
  const name = queryString.parse(query).modal

  if (Array.isArray(name)) {
    return name[0] ?? '';
  }
  return name ?? ''
}

export const ModalManager = () => {
  const location = useLocation();
  useEffect(() => {
  }, [location])

  const modal = getModalName(location.search);
  console.log(modal)
  const Component = modalMap[modal] ?? null;

  return (
    <>
      {
        Component ? <Component /> : null
      }
    </>
  )
}