import React, {useContext, useEffect} from 'react';
import moment from 'moment';
import axios from 'axios';
import Container from '@mui/material/Container';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import {WorkoutHistory} from './types';
import { Button } from '@mui/material';
import {useNavigate} from 'react-router-dom';
import { UserContext } from './Provider';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import IconButton from '@mui/material/IconButton';

const tableContainerStyle = {
  marginBottom: '30px',
  maxHeight: '50vh',
  overflowY: 'scroll',
}

const containerStyle = {
  display: 'flex',
  justifyContent: 'center',
}

const WorkoutHistoryRow = ({entry}: {entry: WorkoutHistory}) => {
  const {targetUser, targetExercise, setTargetExercise, setTargetUser, updateUsers} = useContext(UserContext);

  useEffect(() => {
    if (!targetExercise) {
      const sessionTargetExercise = JSON.parse(sessionStorage.getItem('targetExercise') ?? '');
      setTargetExercise(sessionTargetExercise)
    }

    if (!targetUser) {
      const sessionTargetUserId = sessionStorage.getItem('targetUserId') ?? '';
      setTargetUser(sessionTargetUserId)
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [targetExercise, setTargetExercise, targetUser, setTargetUser])


  const handleHistoryRowDelete = async () => {
    const result = await axios.delete('/userWorkout', {
      params: {
        userId: targetUser?.id ?? '',
        workoutId: targetExercise?.id ?? '',
        userWorkoutId: entry.id,
      }
    })

    if (result.data === "Success!") {
      updateUsers();
    }
  }

  return (
    <TableRow hover={true} sx={{height: 40}}>
      <TableCell>
         {moment(entry.date).format('MMMM Do YYYY')}
      </TableCell>
      <TableCell>
          {entry.weight}
      </TableCell>
      <TableCell>
          {entry.reps}
      </TableCell>
      <TableCell>
          {entry.effort}
      </TableCell>
      <TableCell align="right">
        <IconButton onClick={handleHistoryRowDelete}>
          <DeleteForeverIcon />
        </IconButton>
      </TableCell>
    </TableRow>
  )
}


export const WorkoutHistoryTable = () => {
  const {targetExercise, setTargetExercise, targetUser, setTargetUser} = useContext(UserContext);
  const navigate = useNavigate();

  useEffect(() => {
    if (!targetExercise) {
      const sessionTargetExercise = JSON.parse(sessionStorage.getItem('targetExercise') ?? '');
      console.log('here', {sessionTargetExercise})
      setTargetExercise(sessionTargetExercise)
    }
  }, [targetExercise, setTargetExercise])

  useEffect(() => {
    if (!targetUser) {
      const sessionTargetUserId = sessionStorage.getItem('targetUserId') ?? '';
      setTargetUser(sessionTargetUserId)
    }
  }, [targetUser, setTargetUser])

  const handleAddEntry = () => {
    navigate("/?modal=add-workout-entry")
  }

  const targetWorkout = targetUser?.workouts.find(w => w.id === targetExercise?.id);
  const workoutHistory = targetWorkout?.history ?? [];
  console.log(workoutHistory)

  return (
    <>
      <Typography variant="h6" gutterBottom component="div">
        Past {targetExercise?.name ?? ''} Stats for {targetUser?.name ?? ''}
      </Typography>
      <TableContainer component={Paper} sx={tableContainerStyle}>
        <Table size="small" aria-label="collapsible table">
        <TableHead>
          <TableRow>
            <TableCell align="left">Date</TableCell>
            <TableCell align="left">Weight</TableCell>
            <TableCell align="left">Reps</TableCell>
            <TableCell align="left">Effort</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {workoutHistory.map((entry: WorkoutHistory, i: number) => (
            <WorkoutHistoryRow 
              key={i}
              entry={entry}
            />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    <Container sx={containerStyle}>
      <Button variant="contained" onClick={handleAddEntry}>+ Add Entry</Button>
    </Container>
  </>
  )
}