import React, {useEffect, useContext, useState} from 'react';
import { Button, ButtonGroup, Container, Typography, Grid, TextField } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';

import { UserContext } from './Provider';
import { Exercise } from './types';
import { useNavigate } from 'react-router-dom';


export const SelectExerciseScreen = () => {
  const {exercises, updateExercises, setTargetExercise} = useContext(UserContext);
  const [search, setSearch] = useState('');
  const [filteredExercises, setFilteredExercises] = useState(exercises)
  const navigate = useNavigate();

  useEffect(() => {
    updateExercises();
  }, []);

  useEffect(() => {
    const filtered = exercises.filter(e => e.name.toLowerCase().startsWith(search));
    setFilteredExercises(filtered)
  }, [search, exercises]);

  const handleAddWorkout = () => {

  }

  const handleSelection = (exercise: Exercise) => {
    setTargetExercise(exercise);
    navigate('/?modal=add-workout-entry')
  }

  return (
    <Container>
      <Typography variant="h4" component="div" align="center">
        Choose Exercise
      </Typography>
      <br />
      <Grid container direction="column" spacing={4}>
        <Grid item>
          <Grid container alignItems="center" spacing={2}>
            <Grid item xs={8}>
              <TextField
                fullWidth
                label="Search"
                variant="outlined"
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                />
            </Grid>

            <Grid item xs={4}>
              <Button variant="contained" color="primary" size="large" onClick={handleAddWorkout}>
                  <AddIcon sx={{ mr: 1 }} />
                  Add Exercise
              </Button>
            </Grid>
          </Grid>
        </Grid>
        <Grid item>
        <ButtonGroup size="large" variant="outlined" orientation="vertical" fullWidth>
          {filteredExercises.map((exercise: Exercise, i: number) => (
            <Button 
            key={i}
            onClick={() => handleSelection(exercise)}
            >{exercise.name}</Button>
            ))}
        </ButtonGroup>
        </Grid>
      </Grid>
    </Container>
  )
}